export default {
  data () {
    return {
        show:true
    }
  },
  props:{
      list:Object,
      header:{
          type:Boolean,
          default:true
      },
      button:{
          type:Boolean,
          default:false
      },
      link:{
          type:String
      }
  },
  methods:{
      deletecomment(){
          let obj ={
              id:this.list.comment_id
          }
          const result=this.$http.post('/api/deletecomment',obj)
          result.then((res)=>{
              if(res.data.success){
                  console.log('删除成功')
                  this.show=false
              }else{
                  console.log('删除失败')
              }
          })
      }
  }
}



