export default {
  data () {
    return {
        show:true
    }
  },
  props:{
      list:Object,
      header:{
          type:Boolean,
          default:true
      },
      button:{
          type:Boolean,
          default:false
      },
      link:{
          type:String
      }
  },
  methods:{
      goto(){
          this.$router.push('/content/'+this.list.youji_id)
      },
      deleteyouji(){
          let obj ={
              id:this.list.youji_id
          }
          const result=this.$http.post('/api/deleteyouji',obj)
          result.then((res)=>{
              if(res.data.success){
                  console.log('删除成功')
                  this.show=false
              }else{
                  console.log('删除失败')
              }
          })
      }
  }
}



