import jwt from 'jsonwebtoken'
    export default {
  data () {
    return {
        id:'',
      user:'',
      password:'',
      phone:'',
      email:'',
      message:'',
      show:false
    }
  },
  created(){
      const userinfo=this.getuserinfo();
      this.id=userinfo.id
  },
  methods:{
    modified(){
      let obj ={
          id:this.id,
        name: this.user,
        password: this.password,
        phone:this.phone,
        email:this.email
      }
      const result=this.$http.post('/auth/modified',obj)
        result.then((res)=>{
          if(res.data.success){
            sessionStorage.setItem('demo-token','null')
          console.log('修改成功')
          }else{
          }
        },(err)=>{
          console.log('请求错误'),
           this.message='请求错误'
            this.show=true
        })
    },
    getuserinfo(){
      const token = sessionStorage.getItem('demo-token');
      if(token !=null && token != 'null'){
        let decode = jwt.decode(token);
        return decode
      }else{
        return null
      }
    }
  }
}



