export default {
  data () {
    return {
      user:'',
      password:'',
      phone:'',
      email:'',
      message:'',
      show:false
    }
  },
  methods:{
    logintodo(){
      let obj ={
        name: this.user,
        password: this.password,
        phone:this.phone,
        email:this.email
      }
      const result=this.$http.post('/auth/regsiter',obj)
        result.then((res)=>{
          if(res.data.success){
            this.$router.push('/user')
          console.log('注册成功')
          }else{
            this.message=res.data.info
            this.show=true
          }
        },(err)=>{
          console.log('请求错误'),
           this.message='请求错误'
            this.show=true
        })
    }
  }
}



